
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default {
name: 'DeviceCard',
components: {
Flexbox,
FlexboxItem
},
props: {
device: Object
},
data () {
return {
flexspan: 2 / 3,
hasImage: 1,
img_background: '',
background_size: ''
}
},
mounted () {

if (this.device.images === '') {
this.hasImage = 0
this.flexspan = 0

} else {
this.img_background = '#ccc url("' + this.device.images + '") no-repeat center'
this.background_size = 'auto 100%'

}
}
}



